import React, { useEffect } from "react";
import ScreenHeading from "../../utilities/ScreenHeading/ScreenHeading";
import ScrollService from "../../utilities/ScrollService";
import Animations from "../../utilities/Animations";
import "./AboutMe.css";

export default function AboutMe(props) {
    let fadeInScreenHandler=(screen)=>{
            if(screen.fadeInScreen !== props.id)
            return
            Animations.animations.fadeInScreen(props.id)
        };
    const fadeInSubscription = 
        ScrollService.currentScreenFadeIn.subscribe(fadeInScreenHandler);

        const SCREEN_CONSTSANTS = {
            highlights: {
                bullets:[
                    "Contribute to the development, documentation and understanding of the project scope, objectives and technical feasibility involving all relevant stakeholders",
                    "Ability to write SQL scripts, perform data analysis and interface mapping analysis",
                    "Working knowledge in the use of JIRA/Confluence and Business Project Documentation Lifecycle Gantt Chart",
                    "Exposure on systems implementation and translate business requirements to system requirements",
                    "Currently Learning Web App Development",
                ],
                heading: "Here are a Few Highlights:"
            }
        }
        const renderHighlight = () => {
            return (
                SCREEN_CONSTSANTS.highlights.bullets.map((value, i)=>(
                    <div className='highlight' key={i}>
                        <div className='highlight-blob'></div>
                        <span>{value}</span>
                    </div>
                ))
            )
        }
 
  return (
      <div className="about-me-container screen-container" 
      id={props.id || ""}
      >
          <div className='about-me-parent'>
              <ScreenHeading title={"About Me"} subHeading={"Why Choose Me?"}/>
              <div className='about-me-card'>
                    <div className='about-me-profile'></div>
                    <div className='about-me-details'>
                        <span className='about-me-description'>{SCREEN_CONSTSANTS.description}</span>
                        <div className='about-me-highlights'>
                            <div className='highlight-heading'>
                                <span>{SCREEN_CONSTSANTS.highlights.heading}</span>
                            </div>
                            {renderHighlight()}
                        </div>
                        <div className='about-me-options'>
                        <button className="btn primary-btn"
                        onClick={() => ScrollService.scrollHandler.scrollToHaveAChat()}
                        > 
                        
                        {" "}
                        Have a Chat {""} 
                        </button>
                        
                        </div>
                    </div>
              </div>
          </div>
      </div>
  );
}